import React, { useEffect } from 'react';
//import { Button } from '../ButtonElements';

import {
    InfoContainer,
    InfoWrapper,
    InfoRow,
    Column1,
    Column2,
    TextWrapper,
    TopLine,
    Heading,
    Subtitle,
    Resume,
    //BtnWrap,
    ImgWrap,
    Img
} from './InfoElements';


import Aos from 'aos'
import "aos/dist/aos.css"


const InfoSection = ({
    lightBg,
    id,
    imgStart,
    topLine,
    lightText, 
    headline,
    resume,
    darkText,
    description,
    description2,
    //buttonLabel,
    img,
    alt,
    //primary,
    //dark,
    //dark2
}) => {

    useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])

    return (
        <>
          <InfoContainer lightBg={lightBg} id={id}>
            <InfoWrapper>
                <InfoRow imgStart={imgStart}>
                    <Column1 data-aos="zoom-in-left">
                        <TextWrapper>
                            <TopLine>{topLine}</TopLine>
                            <Heading lightText={lightText}>{headline}</Heading>
                            <Resume>{resume}</Resume>
                            <Subtitle darkText={darkText}>{description}</Subtitle>
                            <Subtitle darkText={darkText}>{description2}</Subtitle>
                            {/*<BtnWrap>
                                <Button to='home'
                                    smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact={true}
                                    offset={-80}
                                    primary={primary ? 1: 0}
                                    dark={dark ? 1 : 0}
                                    dark2={dark2 ? 1 : 0}
                                >
                                    {buttonLabel}</Button>
                            </BtnWrap> */}
                        </TextWrapper>
                    </Column1>
                    <Column2>
                        <ImgWrap data-aos="fade-up">
                            <Img src={img} alt={alt}/>
                        </ImgWrap>
                    </Column2>
                </InfoRow>
            </InfoWrapper>
          </InfoContainer>  
        </>
    );
};

export default InfoSection;
