import React from 'react';
import { AjaDos, Ctat }from './ContactElements';

function ContactItem({icon, text1, text2,title}) {
    return (
        <AjaDos>
            <Ctat>
                <img src={icon} alt="" className="iconcolor"/>
                <div className="right-items">
                    <h6>{title}</h6>
                    <p>{text1}</p>
                    <p>{text2}</p>
                </div>
            </Ctat>
        </AjaDos>
    )
}

export default ContactItem;