import React, { useEffect } from 'react'
import Icon1 from '../../images/meeting.svg';
import Icon2 from '../../images/mockup.svg';
import Icon3 from '../../images/development.svg';
import Icon4 from '../../images/testing.svg';

import {
    ProcesosContainer,
    ProcesosH1,
    ProcesosWrapper,
    ProcesosCard,
    PartOne,
    PartTwo,
    ProcesosIcon,
    ProcesosH2,
    ProcesosP,
} from './ProcesosElementos'


import Aos from 'aos'
import "aos/dist/aos.css"

const Procesos = () => {

    useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])

    return (
        <ProcesosContainer id='services'>
            <ProcesosH1 data-aos="fade-up">Process</ProcesosH1>
            <ProcesosWrapper>
                <ProcesosCard data-aos="zoom-in">
                    <PartOne>
                        <ProcesosIcon src={Icon1} />
                    </PartOne>
                    <PartTwo>
                        <ProcesosH2>FIRST MEETING, BENCHMARKING</ProcesosH2>
                        <ProcesosP>Each stage of the project must be planned. How am I going to do it?. Gathering ideas is one of the most important steps in benchmarking and on which the entire process will depend for optimal web development.</ProcesosP>
                    </PartTwo>               
                </ProcesosCard>
                <ProcesosCard data-aos="zoom-in">
                    <PartOne>
                        <ProcesosH2>SITEMAP, MOCKUP</ProcesosH2>
                        <ProcesosP>When you have all the content, both visual and written, you proceed to create a template with the final result of the web. This template includes everything that the web will be, from its pages, to its written and visual content.</ProcesosP>
                    </PartOne>
                    <PartTwo>
                        <ProcesosIcon src={Icon2} />
                    </PartTwo>
                </ProcesosCard>
                <ProcesosCard data-aos="zoom-in">
                    <PartOne>
                        <ProcesosIcon src={Icon3} />
                    </PartOne>
                    <PartTwo>
                        <ProcesosH2>DEVELOPMENT</ProcesosH2>
                        <ProcesosP>At this point the mockup is mockup in a web version. This is the step in which I take care of the internal characteristics and functions of the page, the internal part of the web is created, such as the operating systems, contents, etc.</ProcesosP>
                    </PartTwo>               
                </ProcesosCard>
                <ProcesosCard data-aos="zoom-in">
                    <PartOne>
                        <ProcesosH2>TESTING, LAUNCHING, REFINEMENT</ProcesosH2>
                        <ProcesosP>Having everything prepared and making sure that the page works correctly, it is time to go to the point that every web developer prefers, the launch. it is common to find elements that need to be corrected.</ProcesosP>
                    </PartOne>
                    <PartTwo>
                        <ProcesosIcon src={Icon4} />
                    </PartTwo>
                </ProcesosCard>
            </ProcesosWrapper>
        </ProcesosContainer>
    );
}
 
export default Procesos;