import React, { useEffect } from 'react';
import {
    FaLinkedin,
    FaGithub,
} from 'react-icons/fa';

import {
    ContactContainer,
    ContactWrapper,
    ContactRow,
    Columnc1,
    Columnc2,
    ContactTextWrapper,
    ContactTopLine,
    ContactHeading,
    ContactSubtitle,
    ContactResume,
    ContactSect,
    
    SocialIcons,
    SocialIconLink,

    ContactImgWrap,
    Img
} from './ContactElements';


import Aos from 'aos'
import "aos/dist/aos.css"


import phone from '../../images/ph.svg';
import email from '../../images/mail.svg';
//import location from '../../images/location.svg';

import ContactItem from './ContactItem';


const Contact = ({
    lightBg,
    id,
    imgStart,
    topLine,
    lightText, 
    headline,
    resume,
    darkText,
   
    img,
    alt,
    
}) => {

    useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])

    return (
        <>
          <ContactContainer lightBg={lightBg} id={id}>
            <ContactWrapper>
                <ContactRow imgStart={imgStart}>
                    <Columnc1 data-aos="zoom-in-left">
                        <ContactTopLine>{topLine}</ContactTopLine>
                            <ContactHeading lightText={lightText}data-aos="zoom-in-up">{headline}</ContactHeading>
                                <ContactImgWrap data-aos="zoom-in-up">
                                        <Img src={img} alt={alt}/>
                                </ContactImgWrap>


                        

                    </Columnc1>
                    <Columnc2>
                    <ContactTextWrapper>
                            <ContactResume>{resume}</ContactResume>
                            <ContactSubtitle darkText={darkText}>
                                <ContactSect>
                                    <ContactItem icon={phone} text1={'+1 7737089312'} title={'Phone'}/>
                                    <ContactItem icon={email} text1={'info@btarache.com'} title={'Email'}/>
                                        <SocialIcons>
                                            <SocialIconLink href='linkedin.com/in/benjamin-tarache-6901a08a' target='_blank'
                                                aria-label='Youtube'>
                                                <FaLinkedin />
                                            </SocialIconLink>
            
                                            <SocialIconLink href='https://github.com/btarache' target='_blank'
                                                aria-label='Linkedin'>
                                                <FaGithub />
                                            </SocialIconLink>
                                        </SocialIcons>
                                </ContactSect>
                            </ContactSubtitle>
                        </ContactTextWrapper>    
                    </Columnc2>
                </ContactRow>
            </ContactWrapper>
          </ContactContainer>  
        </>
    );
};

export default Contact;
