import React, { useEffect, useState } from 'react';
import './Portfolio.css';
import images from './data';
import { SRLWrapper } from 'simple-react-lightbox';



import Aos from 'aos'
import "aos/dist/aos.css"


const options = {
	// settings: {
	// 	overlayColor: 'rgb(25, 136, 124)',
	// 	autoplaySpeed: 1500,
	// 	transitionSpeed: 900
	// },
	// buttons: {
	// 	backgroundColor: 'red',
	// 	iconColor: 'rgba(126, 172, 139, 0.8)'
	// },
	// caption: {
	// 	captionColor: '#a6cfa5',
	// 	captionFontFamily: 'Raleway, sans-serif',
	// 	captionFontWeight: '300',
	// 	captionTextTransform: 'uppercase'
	// },
	// progressBar: {
	// 	height: '20px',
	// 	fillColor: 'blue',
	// 	backgroundColor: 'white'
	// }
};

function Portfolio() {
	const [tag, setTag] = useState('all');
	const [filteredImages, setFilteredImages] = useState([]);

	useEffect(
		() => {
			tag === 'all' ? setFilteredImages(images) : setFilteredImages(images.filter(image => image.tag === tag));
		},
		[tag]
	);
	

	useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])


	return (
		<div className="portafolio" id="portfolio">
            <h1>Portfolio</h1>
			<div className="tags">
				<TagButton name="all" tagActive={tag === 'all' ? true : false} handleSetTag={setTag} /> /
				<TagButton name="web" tagActive={tag === 'web' ? true : false} handleSetTag={setTag} /> /
				<TagButton name="react" tagActive={tag === 'react' ? true : false} handleSetTag={setTag} /> /
				<TagButton name="app" tagActive={tag === 'app' ? true : false} handleSetTag={setTag} /> /
				<TagButton name="design" tagActive={tag === 'design' ? true : false} handleSetTag={setTag} /> /
				<TagButton name="wordpress" tagActive={tag === 'wordpress' ? true : false} handleSetTag={setTag} />
			</div>
			<SRLWrapper options={options}>
				<div className="portaf">
					{filteredImages.map(image => (
						<div key={image.id} className="image-card" data-aos="zoom-in">
							<a href={`/images/${image.imageName}`}>
								<img className="image" src={`/images/${image.imageName}`} alt="" />
							</a>
							<div className="leyenda">
								<a href={image.enlac} target="_blank" rel="noreferrer">
									<p className="describo">{image.text}</p>
								</a>
							</div>
						</div>
					))}
				</div>
			</SRLWrapper>
		</div>
	);
}

const TagButton = ({ name, handleSetTag, tagActive }) => {
	return (
		<button className={`tag ${tagActive ? 'active' : null}`} onClick={() => handleSetTag(name)}>
			{name.toUpperCase()}
		</button>
	);
};

export default Portfolio;
