import React, { useState, useEffect } from 'react';
import Video from '../../videos/video.mp4';
import { Button } from '../ButtonElements';
import { 
    HeroContainer,
    HeroBg,
    VideoBg,
    HeroContent,
    HeroH1,
    HeroP,
    HeroBtnWrapper,
    ArrowForward,
    ArrowRight

} from './HeroElements';


import Aos from 'aos'
import "aos/dist/aos.css"


const HeroSection = () => {

    useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])

    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)

    }

    return (
        <HeroContainer id='home'>
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video/mp4' />
            </HeroBg>
            <HeroContent>
                <HeroH1 data-aos="zoom-in-up">
                    Benjamin Tarache
                </HeroH1>
                <HeroP>
                Web Developer
                </HeroP>
                <HeroBtnWrapper>
                    <Button 
                        to="about"
                        onMouseEnter={onHover} 
                        onMouseLeave={onHover}
                        primary="true"
                        dark="true"
                        smooth={true}
                        duration={500}
                        spy={true}
                        exact='true'
                        offset={-80}
                    >
                        Get Started {hover ? <ArrowForward /> : <ArrowRight />}
                    </Button>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    );
};

export default HeroSection;
