import styled from 'styled-components';

export const ContactContainer = styled.div`
    color: #fff;
    background: ${({lightBg}) => (lightBg ? '#3c1252' : '#010606')};

    @media screen and (max-width: 768px) {
        padding: 100px 0;
    }
`;

export const ContactWrapper = styled.div`
    display: grid;
    z-index: 1;
    min-height: 100vh;
    width: 100%;
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 24px;
    justify-content: center;

`;

export const ContactRow = styled.div`
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    grid-template-areas: ${({imgStart}) => (imgStart ? `'col2 col1'` : `'col1 col2'`)};

    @media screen and (max-width: 768px) {
        grid-template-areas: ${({imgStart}) => (imgStart ? `'col1' 'col2'` : `'col1 col1' 'col2 col2'`)}
    }
`; 

export const Columnc1 = styled.div`
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col1;
`;

export const Columnc2 = styled.div`
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col2;
`;

export const ContactTextWrapper = styled.div`
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;
`;

export const ContactTopLine = styled.p`
    color: #ffcc00;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
`;

export const ContactResume = styled.p`
    color: #ffcc00;
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
`;

export const ContactHeading = styled.h1`
    
    margin-bottom: 24px;
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    color: ${({lightText}) => (lightText ? '#f7f8fa' : '#fff') };
    text-align: center;
    
    @media screen and (max-width: 768px) {
        font-size: 32px;
    }
`;

export const ContactSubtitle = styled.p`
    max-width: 440px;
    margin-bottom: 35px;
    font-size: 16px;
    line-height: 28px;
    color: ${({darkText}) => (darkText ? '#010606' : '#c3c3c3')};
`;

export const ContactBtnWrap = styled.div`
    display: flex;
    justify-content: flex-start;
`;

export const ContactImgWrap = styled.div`
    max-width: 555px;
    height: 100%;
`;

export const Img = styled.img`
    width: 100%;
    margin: 0 0 10px 0;
    padding-right: 0;
    fill: #fff;
`;


export const ContactSect = styled.div`
    display: flex;
    justify-content: space-between;
    flex-direction: column;
`;

export const AjaDos = styled.div`

    width: 100%;
    &:not(:last-child){
        margin-bottom: 1rem;
    }
    

`;

export const Ctat = styled.div`

    display: flex;
    align-items: center;
    background-color: #310f41;
    padding: 2rem 0;
    color: #c3c3c3;
    .right-items{
        margin-left: 2rem;
    }

    @media screen and (max-width: 768px) {
        padding: 1.5rem 0rem;
        .right-items {
            margin-left: 1rem;
        }
    }

    img{
        padding: 1rem;
        border: 1px solid rgba(255,255,255,0.1);
        margin-left: 3rem;
        width: 16%;
        color: #fff;
    }
    .right-items{
        h6{
            font-size: 1.4rem;
            font-weight: 500;
            color: #45d4c2;
        }
    }
    p {
        color: #c3c3c3;
        font-size: 0.9rem;
    }
    .iconcolor {
        fill: #fff;
    }

`;



export const SocialIcons = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #310f41;
    padding: 3rem;
    color: #c3c3c3;
    width: 100%;

    @media screen and (max-width: 990px) {
        justify-content: center;
        width: 100%;
    }
`;

export const SocialIconLink = styled.a`
    color: #fff;
    font-size: 34px;
    padding-right: 20px;

    &:hover {
        color: #ffcc00;
    }

    @media screen and (max-width: 990px) {
        padding-left: 20px;
    }
`;