import React from 'react';
import { animateScroll as scroll } from 'react-scroll';

import Logo from '../../images/logo.svg';

import { 
    FooterContainer,
    FooterWrap,
    FooterLinksContainer,
    FooterLinksWrapper,
    FooterLinkItems,
    FooterLinkTitle,
    //FooterLink,
    SocialMedia,
    SocialMediaWrap,
    SocialLogo,
    WebsiteRights,
    SocialIcons,
    //SocialIconLink,

    
 } from './FooterElements';
 //import bgfooter from "../../images/chicago.svg";

const Footer = () => {


    
    const toggleHome = () => {
        scroll.scrollToTop()
    };




    return (
        <FooterContainer >
            <FooterWrap>
                <div>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                        <SocialLogo
                        to='/' onClick={toggleHome}>
                            <img src={Logo} alt="BT"
                                style={{width: '120px'}}
                            />
                        </SocialLogo>
                            <FooterLinkTitle>Benjamin Tarache Web Development</FooterLinkTitle>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                </div>
                <div>
                <SocialMedia>
                    <SocialMediaWrap>
                        
                        <WebsiteRights>Copyright &copy; 2020 - {new Date().getFullYear()}, 
                          All Rights reserved</WebsiteRights>
                        <SocialIcons>
                            Chicago, USA
                        </SocialIcons>
                    </SocialMediaWrap>
                </SocialMedia>
                </div>

            </FooterWrap>
        </FooterContainer>
    );
};

export default Footer;
