import React, { useEffect } from 'react';
import Icon1 from '../../images/dsign.svg';
import Icon2 from '../../images/rsponsiv.svg';
import Icon3 from '../../images/wp.svg';
import {
    ServicesContainer,
    ServicesH1,
    ServicesWrapper,
    ServicesCard,
    ServicesIcon,
    ServicesH2,
    ServicesP,
} from './ServicesElements';


import Aos from 'aos'
import "aos/dist/aos.css"



const Services = () => {

    useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])


    return (
        <ServicesContainer id='services'>
            <ServicesH1>Services</ServicesH1>
            <ServicesWrapper>
                <ServicesCard data-aos="flip-left">
                    <ServicesIcon src={Icon1} />
                    <ServicesH2>Web Development</ServicesH2>
                    <ServicesP>Your website is built from the ground up according to your technical specifications, keeping you informed and involved throughout the process.</ServicesP>
                </ServicesCard>

                <ServicesCard data-aos="flip-up">
                    <ServicesIcon src={Icon2} />
                    <ServicesH2>Responsive Design</ServicesH2>
                    <ServicesP>Responsive Web Design (RWD) means you only need one site for all devices which is far cheaper than developing and maintaining multiple sites.</ServicesP>
                </ServicesCard>

                <ServicesCard data-aos="flip-right">
                    <ServicesIcon src={Icon3} />
                    <ServicesH2>WordPress Development</ServicesH2>
                    <ServicesP>WordPress is a dynamic content management system (CMS), is at the heart of every good website, making it easy to keep your site up-to-date and safe.</ServicesP>
                </ServicesCard>
            </ServicesWrapper>
        </ServicesContainer>
    );
};

export default Services;
