import React, { useEffect } from 'react'
import  { SkillsContainer, SkillsH1, SkillsWrapper, IconS} from './SkillsElements';
import {
    FaGithub,
    FaHtml5,
    FaCss3Alt,
    FaSass,
    FaReact,
    FaWordpress,
    FaBootstrap,
} from 'react-icons/fa';

import { SiGatsby } from 'react-icons/si';
import { MdGridOn } from 'react-icons/md';
import { DiPhotoshop, DiIllustrator } from 'react-icons/di';
import { SiAdobeindesign } from 'react-icons/si';

import IconWithTooltip from "icon-with-tooltip";



import Aos from 'aos'
import "aos/dist/aos.css"

const Skills = () => {

    useEffect(() => {
		Aos.init({ duration: 2000 });
	}, [])

    return (
        <>
            <SkillsContainer>
                <SkillsH1>Skills</SkillsH1>
                <SkillsWrapper data-aos="zoom-in">

                
                
                    <IconS>
                        <IconWithTooltip Icon={FaHtml5} text="HTML5" placement="top" />
                        <p>HTML</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={FaCss3Alt} text="CSS3" placement="top" />
                        <p>CSS</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={FaReact} text="React" placement="bottom" />
                        <p>React JS</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={FaSass} text="Sass" placement="bottom" />
                        <p>Sass</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={FaWordpress} text="WordPress" placement="bottom" />
                        <p>WordPress</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={FaBootstrap} text="Bootstrap" placement="bottom" />
                        <p>Bootstrap</p>
                    </IconS>

                    <IconS>
                        <IconWithTooltip Icon={FaGithub} text="Github" placement="bottom" />
                        <p>GitHub</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={SiGatsby } text="Gatsby" placement="bottom" />
                        <p>Gatsby</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={MdGridOn } text="Grid" placement="bottom" />
                        <p>CSS-Grid</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={DiPhotoshop } text="Photoshop" placement="bottom" />
                        <p>Photoshop</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={DiIllustrator } text="Illustrator" placement="bottom" />
                        <p>Illustrator</p>
                    </IconS>
                    <IconS>
                        <IconWithTooltip Icon={SiAdobeindesign } text="Indesign" placement="bottom" />
                        <p>Indesign</p>
                    </IconS>
                        
                        
                    

                </SkillsWrapper>
            </SkillsContainer>   
        </>
    )
}
export default Skills;
