import styled from 'styled-components';

export const ProcesosContainer = styled.div`
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #3c1252;
    padding-bottom: 5rem;
    margin-top: 0rem;
    color: #fff;

    @media screen and (max-width: 768px) {
        height: auto;
    }

    @media screen and (max-width: 480px) {
        height: auto;
    }

`;


export const ProcesosWrapper = styled.div`
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
	grid-template-rows: repeat(1fr, 1fr, 1fr, 1fr);
    align-items: center;
    grid-gap: 16px;
    padding: 0 50px;

    @media screen and (max-width: 1000px) {
        grid-template-columns: 1fr 1fr;
    }

    @media screen and (max-width: 990px) {
        grid-template-columns: 1fr;
        padding: 0 20px;
    }
`;


export const ProcesosCard = styled.div`
    background: #310f41;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    border-radius: 10px;
    height: auto;
    padding: 30px;
    box-shadow: 0 1px 3px rgba(255,255,255,0.4);
    transition: all 0.2s ease-in-out;

    &:hover {
        transform: scale(1.02);
        transition: all 0.2s ease-in-out;
        cursor: pointer;
    }

    @media screen and (max-width: 768px) {
    flex-direction: column;
}

`;



export const PartOne = styled.div`
    text-align: center;
`;
export const PartTwo = styled.div`
    text-align: center;
`;

export const ProcesosIcon = styled.img`
    height: auto;
    width: 250px;
    margin-bottom: 10px;

    @media screen and (max-width: 768px) {
        width: 150px;
    }
`;


export const ProcesosH1 = styled.h1`
    font-size: 3rem;
    color: #fff;
    margin: 72px 0 30px;

    @media screen and (max-width: 990px) {
        font-size: 2rem;
    }
`;

export const ProcesosH2 = styled.h2`
    font-size: 1rem;
    margin-bottom:25px;
    color: #45d4c2;
`;

export const ProcesosP = styled.p`
    font-size: 1rem;
    text-align: center;
    padding: 0 1rem 1rem;
`;